import { NextResponse } from "next/server";

export async function POST(req: Request) {
  const { prompt } = await req.json();

  try {
    const response = await fetch(
      "https://api-inference.huggingface.co/models/LGAI-EXAONE/EXAONE-Deep-2.4B",
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${process.env.HF_API_KEY}`.trim(), // üîë ÌôòÍ≤Ω Î≥ÄÏàò
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ inputs: prompt }),
      }
    );

    // üîç ÏùëÎãµ ÌôïÏù∏
    const text = await response.text();
    console.log("Hugging Face API Raw Response:", text);

    // ‚úÖ HTML Ïò§Î•ò ÌéòÏù¥ÏßÄ Î∞©ÏßÄ
    if (!response.ok) {
      return NextResponse.json(
        { error: `Hugging Face API Error: ${text}` },
        { status: response.status }
      );
    }

    // ‚úÖ JSON ÌååÏã± ÏãúÎèÑ
    try {
      const result = JSON.parse(text);
      return NextResponse.json({
        response: result[0]?.generated_text || "No response",
      });
    } catch (jsonError) {
      console.error("JSON Parse Error:", jsonError);
      return NextResponse.json(
        { error: "Invalid JSON response from API" },
        { status: 500 }
      );
    }
  } catch (error) {
    console.error("API Error:", error);
    return NextResponse.json({ error: "API request failed" }, { status: 500 });
  }
}
